


























































































import { Route } from 'vue-router'
import { defineComponent, PropType } from '@vue/composition-api'

// Composables
import { UseDateRange } from '@/use/date-range'
import { UseOrder } from '@/use/order'
import type { OverviewItem } from '@/use/system-stats'

// Components
import ThOrder from '@/components/ThOrder.vue'
import SparklineChart from '@/components/SparklineChart.vue'

// Utilities
import { xkey } from '@/models/otelattr'
import { quote } from '@/util/string'
import { percent } from '@/util/fmt'

export default defineComponent({
  name: 'OverviewTable',
  components: {
    ThOrder,
    SparklineChart,
  },

  props: {
    dateRange: {
      type: Object as PropType<UseDateRange>,
      required: true,
    },
    loading: {
      type: Boolean,
      required: true,
    },
    items: {
      type: Array as PropType<OverviewItem[]>,
      default: () => [],
    },
    order: {
      type: Object as PropType<UseOrder>,
      required: true,
    },
    column: {
      type: String,
      default: '',
    },
    baseColumnRoute: {
      type: Object as PropType<Route>,
      default: undefined,
    },
  },

  setup(props) {
    function columnRoute(value: string) {
      let where: string

      if (value === '') {
        where = `${props.column} does not exist`
      } else {
        where = `${props.column} = ${quote(value)}`
      }

      const route = { ...props.baseColumnRoute }
      route.query = {
        ...route.query,
        where,
      }

      return route
    }

    function groupListRoute(system: string) {
      return {
        name: 'GroupList',
        query: {
          ...props.dateRange.queryParams(),
          system,
        },
      }
    }

    return {
      xkey,

      columnRoute,
      groupListRoute,
      percent,
    }
  },
})
