






import { defineComponent, computed } from '@vue/composition-api'

import Prism from 'vue-prism-component'

export default defineComponent({
  name: 'XCode',
  components: { Prism },

  props: {
    code: {
      type: String,
      required: true,
    },
    language: {
      type: String,
      default: 'markup',
    },
    inline: {
      type: Boolean,
      default: false,
    },
  },

  setup(props) {
    const wrap = computed((): boolean => {
      return !props.code.includes('\n')
    })

    return { wrap }
  },
})
