
import Vue from 'vue'

// Utilities
import { unitFromName, createFormatter } from '@/util/fmt'

export default Vue.component('XNum', {
  functional: true,
  props: {
    value: {
      type: undefined,
      required: true,
    },
    name: {
      type: String,
      default: '',
    },
  },
  render(h, { props }) {
    const unit = unitFromName(props.name, props.value)
    const fmt = createFormatter(unit)
    return h('span', fmt(props.value))
  },
})
