


























































































import { defineComponent, computed, watch, proxyRefs } from '@vue/composition-api'

// Components
import LoadPctileChart from '@/components/LoadPctileChart.vue'
import SystemBarChart from '@/components/SystemBarChart.vue'
import TraceTimeline from '@/components/TraceTimeline.vue'
import TraceError from '@/components/TraceError.vue'

// Composables
import { useTitle } from '@vueuse/core'
import { useDateRange, UseDateRange } from '@/use/date-range'
import { useTrace, UseTrace } from '@/use/trace'

// Utilities
import { xkey } from '@/models/otelattr'
import { hour } from '@/util/date'

export default defineComponent({
  name: 'TraceShow',
  components: { LoadPctileChart, SystemBarChart, TraceTimeline, TraceError },

  setup() {
    useTitle('View trace')
    const dateRange = useDateRange()
    const trace = useTrace()

    const axiosParams = computed(() => {
      if (!trace.root) {
        return
      }
      return {
        ...dateRange.axiosParams(),
        system: trace.root.system,
        group_id: trace.root.groupId,
      }
    })

    const groupRoute = computed(() => {
      if (!trace.root) {
        return
      }

      return {
        name: 'GroupList',
        query: {
          ...dateRange.queryParams(),
          system: trace.root.system,
          where: `${xkey.spanGroupId} = "${trace.root.groupId}"`,
        },
      }
    })

    const exploreTraceRoute = computed(() => {
      if (!trace.root) {
        return
      }

      return {
        name: 'GroupList',
        query: {
          ...dateRange.queryParams(),
          system: xkey.allSystem,
          query: [
            `group by ${xkey.spanGroupId}`,
            xkey.spanCount,
            xkey.spanErrorCount,
            `p50(${xkey.spanDuration})`,
            `p90(${xkey.spanDuration})`,
            `p99(${xkey.spanDuration})`,
            `sum(${xkey.spanDuration})`,
            `where ${xkey.spanTraceId} = "${trace.root.traceId}"`,
          ].join(' | '),
          plot: null,
        },
      }
    })

    watch(
      () => trace.root,
      (span) => {
        if (span) {
          useTitle(span.name)
          dateRange.changeWithin(span.time, hour)
        }
      },
    )

    return {
      dateRange,
      trace,
      meta: useMeta(dateRange, trace),

      axiosParams,
      groupRoute,
      exploreTraceRoute,
    }
  },
})

function useMeta(dateRange: UseDateRange, trace: UseTrace) {
  const breadcrumbs = computed(() => {
    const root = trace.root
    const bs: any[] = []

    if (!root) {
      return bs
    }

    if (root.system) {
      bs.push({
        text: root.system,
        to: {
          name: 'GroupList',
          query: {
            ...dateRange.queryParams(),
            system: root.system,
          },
        },
        exact: true,
      })
    }

    if (root.system && root.groupId) {
      bs.push({
        text: root.name,
        to: {
          name: 'GroupList',
          query: {
            ...dateRange.queryParams(),
            system: root.system,
            where: `${xkey.spanGroupId} = "${root.groupId}"`,
          },
        },
        exact: true,
      })
    }

    bs.push({
      text: trace.id,
    })

    return bs
  })

  return proxyRefs({ breadcrumbs })
}
